.element-button-status {
  align-items: center;
  border: 1px solid;
  border-radius: 4px;
  display: flex;
  gap: 8px;
  justify-content: center;
  position: relative;
  width: fit-content;
}

.element-button-status .element-loader {
  align-items: center;
  background-color: #ffffff00;
  display: flex;
  flex-direction: column;
  gap: 8px;
  position: relative;
  width: fit-content;
}

.element-button-status .svg-container {
  background-size: 100% 100%;
  height: 12px;
  min-width: 12px;
  position: relative;
}

.element-button-status .svg {
  height: 6px;
  left: 6px;
  position: absolute;
  top: 6px;
  width: 6px;
}

.element-button-status .button {
  font-family: "Inter", Helvetica;
  font-weight: 600;
  letter-spacing: 0;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.element-button-status.focus {
  overflow: hidden;
}

.element-button-status.SM {
  height: 24px;
  padding: 4px 12px;
}

.element-button-status.LG {
  height: 40px;
  padding: 12px 24px;
}

.element-button-status.disable {
  background-color: #d0d5dd;
  border-color: #d0d5dd;
}

.element-button-status.MD {
  height: 32px;
  padding: 4px 16px;
}

.element-button-status.active.success {
  background-color: #024829;
  border-color: #024829;
}

.element-button-status.focus.success {
  background-color: #3ccb7f;
  border-color: #3ccb7f;
  box-shadow: 0px 0px 0px 2px #09925040, 0px 1px 3px #0000001a, 0px 1px 2px #0000000f;
}

.element-button-status.danger.active {
  background-color: #5f1408;
  border-color: #5f1408;
}

.element-button-status.default.success {
  background-color: #16b364;
  border-color: #16b364;
}

.element-button-status.warning.focus {
  background-color: #fdb022;
  border-color: #fdb022;
  box-shadow: 0px 0px 0px 2px #dc680340, 0px 1px 3px #0000001a, 0px 1px 2px #0000000f;
}

.element-button-status.default.danger {
  background-color: #f04438;
  border-color: #f04438;
}

.element-button-status.danger.loading {
  background-color: #f04438;
  border-color: #f04438;
}

.element-button-status.hover.danger {
  background-color: #d92d20;
  border-color: #d92d20;
}

.element-button-status.danger.focus {
  background-color: #f97066;
  border-color: #f97066;
  box-shadow: 0px 0px 0px 2px #d92d2040, 0px 1px 3px #0000001a, 0px 1px 2px #0000000f;
}

.element-button-status.default.warning {
  background-color: #f79009;
  border-color: #f79009;
}

.element-button-status.active.warning {
  background-color: #652104;
  border-color: #652104;
}

.element-button-status.loading.success {
  background-color: #16b364;
  border-color: #16b364;
}

.element-button-status.hover.success {
  background-color: #099250;
  border-color: #099250;
}

.element-button-status.loading.warning {
  background-color: #f79009;
  border-color: #f79009;
}

.element-button-status.hover.warning {
  background-color: #dc6803;
  border-color: #dc6803;
}

.element-button-status.warning .svg-container {
  background-image: url(../../../static/img/background-5.svg);
}

.element-button-status.danger .svg-container {
  background-image: url(../../../static/img/background-2.svg);
}

.element-button-status.success .svg-container {
  background-image: url(../../../static/img/background-8.svg);
}

.element-button-status.active .button {
  color: #ffffff;
}

.element-button-status.loading .button {
  color: #ffffff;
}

.element-button-status.hover .button {
  color: #ffffff;
}

.element-button-status.focus .button {
  color: #ffffff;
}

.element-button-status.SM .button {
  font-size: 12px;
  line-height: 19.2px;
  margin-bottom: -0.5px;
  margin-top: -2.5px;
}

.element-button-status.default .button {
  color: #ffffff;
}

.element-button-status.LG .button {
  font-size: 14px;
  line-height: 22.4px;
  margin-bottom: -2px;
  margin-top: -4px;
}

.element-button-status.disable .button {
  color: #98a2b3;
}

.element-button-status.MD .button {
  font-size: 12px;
  line-height: 19.2px;
}
